.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.blank{
  
  display: flex;
  justify-content: center;
  align-items: center;
  color: #61dafb;
  margin-left: 2rem;
  font-size: 2vw;
  font-weight: 400;
}
nav{
  display: block;
  width: 100%;
  padding: 0;
  border-radius: 2px;
  color: white;
  display: flex;
  background-color: rgb(221, 45, 45);
  align-items: center;
}
nav ul{
  justify-content: center;
  align-items: center;
  display: flex;
  margin-left: 2rem;
  
}
nav p{
  margin-left: 1rem;
  font-weight: bold;
  
}

nav li{
  list-style: none;
  margin-left: 2rem;
  cursor: pointer;
}
nav li:hover{
  color: blue;
}
section{
  position: relative;
  display: flex;
}
section .left{
  width: 50%;
  position: relative;
  padding: 0 2rem;
  margin-left: 3rem;
}
section .right{
  width: 50%;
  position: relative;
  padding: 0 2rem;
  margin-right: 1rem;
}